  ambilpesanan(row) {
    let pembyaran = [];
    row.pembayaran.forEach(element => {
      let row = {
        jenis : element.jenis,
        keterangan : element.keterangan,
        nominal: element.jumlah_rp,
        status : "DONE"
      }
      pembyaran.push(row)
    });
    setItem('trx_pemabayran_ambil_pesanan', pembyaran)
    postData2("pesanan/ambil/no", { no_pesanan: row.no_pesanan }).then((res) => {
      setItem('data_barang_ambil_pesanan', res.data)
      this.props.dispatch(showModal());
    }).catch((err) => {
      ToastNotification('info', 'Terjadi Kesalahaan Saat Request Data')
    })
   
  }

