  batalPesanan(row) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Membatalkan Pesanan Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let tanggal = getItem("tgl_laporan");
        this.setState({
          isLoading: true,
        });
        putData2("pesanan/cancel", { no_pesanan: row.no_pesanan })
          .then((res) => {
            this.setState({
              isLoading: false,
            });
            ToastNotification("success", "Pesanan Berhasil Di Batal");
            getData2(
              "pesanan/" + tanggal.tgl_awal + "/" + tanggal.tgl_akhir
            ).then((res) => {
              // console.log(res.data);
              let pesanan = [];
                res.data.forEach((element) => {
                  let row = {
                    no_pesanan: element.no_pesanan,
                    nama_customer: element.nama_customer,
                    no_hp: element.no_hp,
                    sisa_bayar: element.sisa_bayar,
                    jumlah_bayar: element.jumlah_bayar,
                    total_harga: element.total_harga,
                    tanggal_transaksi: element.tanggal,
                    detail_barang: element.detail_barang,
                    pembayaran: element.pembayaran,
                    status_pesanan: element.status_pesanan,
                  };
                  pesanan.push(row);
                });
              this.setState({
                datapesanan: pesanan,
                isLoading: false,
              });
            });
          })
          .catch((error) => {
            this.setState({
              isLoading: false,
            });
            ToastNotification("info", /conflict/i.test(error.response.data.message || "Error") ? "Terjadi Kesalahan Saat Mengirim Data" : error.response.data.message);
          });
      }
    });
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    getData2("pesanan/" + data.tgl_awal + "/" + data.tgl_akhir)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification("info", "Data Pesanan Tidak Ada");
        }
        // res
        let pesanan = [];
        res.data.forEach((element) => {
          let row = {
            no_pesanan: element.no_pesanan,
            nama_customer: element.nama_customer,
            no_hp: element.no_hp,
            sisa_bayar: element.sisa_bayar,
            jumlah_bayar: element.jumlah_bayar,
            total_harga: element.total_harga,
            tanggal_transaksi: element.tanggal,
            detail_barang: element.detail_barang,
            pembayaran: element.pembayaran,
            status_pesanan: element.status_pesanan,
          };
          pesanan.push(row);
        });

        let data_post = {
          tgl_awal: formatDate(
            moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
          ),
          tgl_akhir: formatDate(
            moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
          ),
        };
        this.setState({
          isLoading: false,
          datapesanan: pesanan,
        });
        setItem("tgl_laporan", data_post);
      })
      .catch((error) => {
        ToastNotification("info", /conflict/i.test(error.response.data.message || "Error") ? "Terjadi Kesalahan Saat Mengirim Data" : error.response.data.message);
        this.setState({
          isLoading: false,
        });
      });
  }
  componentDidMount() {
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
}

