import {
  React,
  Component,
  connect,
  Field,
  createNumberMask,
  formValueSelector,
  reduxForm,
  NumberOnly,
  localStoragedecryp,
  ReanderField,
  ReanderSelect,
} from "../../../components/helpers/library";
import { getDataSales } from "../../../actions/datamaster_action";
import ValidasiPembayaran from "../../../Validasi/Penjualan/ValidasiPembayaran";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
const jenis_pembayaran = [
  {
    value: "CASH",
    name: "CASH",
  },
  {
    value: "DEBET",
    name: "DEBET",
  },
  {
    value: "TRANSFER",
    name: "TRANSFER",
  },
  {
    value: "CREDIT",
    name: "CREDIT",
  },
];
class HeadInputPembayaran extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      type_pembayaran: "CASH",
      columns: [
        // {
        //   dataField: "nama_bank",
        //   text: "Nama Bank",
        //   footerAttrs: {
        //     colSpan: "2",
        //   },
        //   footer: "Grand Total",
        // },
        {
          dataField: "type",
          text: "Type",
          footerAttrs: {
            colSpan: "1",
          },
          footer: "Grand Total",
        },
        {
          dataField: "Total",
          text: "Total",
          footer: "",
        },
      ],
    };
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
    let data_barang_penjualan = JSON.parse(
      localStoragedecryp("data_barang_ambil_pesanan")
    );

    setTimeout(() => {
      // console.log();
      let data = JSON.parse(localStorage.getItem("trx_pemabayran_ambil_pesanan")) || [];
      let total = 0;
      data.forEach((element) => {
        total = element.nominal + total;
      });
      let total_bayar = NumberOnly(document.getElementById('grand_total_pembayaran').value);
      this.props.change('harus_bayar_rp',parseInt(total_bayar) - parseInt(total))
    }, 100);

    // let total_dp = getItem("total_dp");
    // this.props.change("total_dp", total_dp);
  
    this.props.change("grand_total_pembayaran", data_barang_penjualan.total_harga);
    // this.props.change("harus_bayar_rp", total);
    this.props.change("jenis", "CASH");
    setTimeout(() => {
      document.getElementById("cash_trx_penjualan").focus();
    }, 100);
  }
  hitungTotal() {
    // console.log(this.props.nominal_kembali);
    this.props.change("kembali", this.props.kembali);
    this.props.change("nominal_kembali", this.props.nominal_kembali);
  }

  pilihPembyaran(e) {
    if (e === "CASH") {
      this.setState({
        type_pembayaran: "CASH",
      });
    } else if (e === "TRANSFER") {
      this.setState({
        type_pembayaran: "TRANSFER",
      });
    } else if (e === "DEBET") {
      this.setState({
        type_pembayaran: "DEBET",
      });
    } else {
      this.setState({
        type_pembayaran: "CREDIT"
      })
    }
    this.props.change("cash", "");

  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-6">
            <Field
              id="grand_total_pembayaran"
              name="grand_total_pembayaran"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              readOnly={true}
              {...currencyMask}
              label="Total Harga Jual"
              placeholder="Total Harga Jual"
            />
          </div>
          
          <div className="col-6">
            <Field
              id="harus_bayar_rp"
              name="harus_bayar_rp"
              component={ReanderField}
              type="text"
              onChange={this.hitungTotal()}
              readOnly={true}
              {...currencyMask}
              label="Harus Bayar Rp"
              placeholder="Harus Bayar Rp"
            />
          </div>
          <div className="col-6">
            <Field
              name="jenis"
              label="Pilih Jenis Bayar"
              placeholder="Pilih Jenis Pembayaran"
              options={jenis_pembayaran}
              onChange={(e) => this.pilihPembyaran(e)}
              component={ReanderSelect}
            />
          </div>

          <div
            className="col-6"
            style={{
              display:
                this.state.type_pembayaran === "CASH"
                  ? "none"
                  : this.state.type_pembayaran === "DEBET"
                  ? "block"
                  : "block",
            }}
          >
            <Field
              tabIndex="1"
              name="nama_bank"
              id="nama_bank"
              component={ReanderField}
              type="text"
              label="Bank"
              placeholder="Masukan Nama Bank"
            />
          </div>
          <div
            className="col-6"
            style={{
              display:
                this.state.type_pembayaran === "CASH"
                  ? "none"
                  : this.state.type_pembayaran === "DEBET"
                  ? "none"
                  : "block",
            }}
          >
            <Field
              tabIndex="1"
              name="no_card"
              id="no_card"
              component={ReanderField}
              type="text"
              label="Card No"
              placeholder="Masukan Card No"
            />
          </div>
          <div className="col-6">
            <Field
              tabIndex="1"
              name="cash"
              id="cash_trx_penjualan"
              component={ReanderField}
              type="text"
              label="Nominal"
              onChange={this.hitungTotal()}
              {...currencyMask}
              normalize={NumberOnly}
              placeholder="Masukan Nominal"
            />
          </div>
          <div className="col-6" 
          style={{
              display:
                this.state.type_pembayaran === "CREDIT"
                  ? "block"
                  : "none"
            }}>
            <Field
              tabIndex="1"
              name="fee"
              id="fee"
              component={ReanderField}
              type="text"
              label="Fee (%)"
              normalize={NumberOnly}
              placeholder="5"
            />
          </div>
          <div className="col-6 d-none">
            <Field
              tabIndex="1"
              name="nominal_kembali"
              component={ReanderField}
              type="text"
              label="Nominal"
              placeholder="Masukan Nominal"
            />
          </div>
          <div className="col-6">
            <Field
              name="kembali"
              id="sisa_bayar"
              component={ReanderField}
              type="text"
              readOnly={true}
              label="Sisa"
              //   {...currencyMask}
              normalize={NumberOnly}
              placeholder="Sisa Pembayaran"
            />
          </div>
          <div
            className={
              this.state.type_pembayaran === "CASH"
                ? "col-6 text-right"
                : this.state.type_pembayaran === "TRANSFER"
                ? "col-6 text-right"
                : "col-12 text-right"
            }
          >
            <br />
            <label>&nbsp;</label>
            <button tabIndex="2" className="btn btn-primary">
              {" "}
              <i className="fa fa-plus"></i>
            </button>
          </div>
        </div>
      </form>
    );
  }
}

HeadInputPembayaran = reduxForm({
  form: "HeadInputPembayaran",
  enableReinitialize: true,
  validate: ValidasiPembayaran,
})(HeadInputPembayaran);

const selector = formValueSelector("HeadInputPembayaran"); // <-- same as form name

export default connect((state) => {
  const { cash,grand_total_pembayaran } = selector(
    state,
    "grand_total_pembayaran",
    "cash"
  );
  let data = JSON.parse(localStorage.getItem("trx_pemabayran_ambil_pesanan")) || [];
  let total = 0;
  data.forEach((element) => {
    total = element.nominal + total;
  });
  let hasil = parseFloat(total || 0) - parseFloat(grand_total_pembayaran || 0);
  return {
    harus_bayar_rp: hasil,
    kembali: hasil.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    nominal_cash: cash,
    nominal_kembali : hasil + parseInt(cash)
  };
})(HeadInputPembayaran);
