import {
  React,
  reduxForm,
  Component,
  connect,
  getItem,
  setItem,
  Tabel,
  Swal,
  ToastNotification,
} from "../../../components/helpers/library";

import ValidasiPembayaran from "../../../Validasi/Penjualan/ValidasiPembayaran";
import HeadInputPembayaran from "./HeadInputPembayaran";

// data_barang_penjualan_tmp

const maptostate = (state) => {
  let data_barang = getItem("data_barang_pesanan") || [];
  let status = "PENJUALAN";
  data_barang.forEach((element) => {
    if (element.no_titip_group === "-") {
      status = "PENJUALAN";
    } if (element.no_pesanan === "-") {
      status = "PESANAN";
    } else {
      status = "TITIPAN";
    }
  });
  return {
    status: status,
  };
};
class ModalPembayaran extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      type_pembayaran: "CASH",
      kembalian: "",
      columns: [
        {
          dataField: "jenis",
          text: "Jenis",
          footerAttrs: {
            colSpan: "2",
          },
          footer: "Grand Total",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },
        {
          dataField: "nominal",
          text: "Total",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("kr-KO"),
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    disabled={row.status === "DONE" ? true : false}
                    onClick={() => this.hapusdata(index)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
  }
  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      // console.log(id);
      if (result.isConfirmed) {
        let data = getItem("trx_pemabayran_ambil_pesanan");
        data.splice(id, 1);
        setItem("trx_pemabayran_ambil_pesanan", data);
        this.setState({
          status: "berhasil",
        });
        // deleteLocalItemBarcode("data_barang_hancur_tmp", id);
        // this.props.dispatch(getListReturnSupplier());
      }
    });
  }
  simpanPembayaran(row) {
    if(row.no_card === undefined && row.jenis !== "CASH" && row.jenis !== "DEBET"){
      ToastNotification("info", "Nomor kartu tidak boleh kosong");
      return false;
    }
    let datacek = {
      total_transaksi : row.grand_total_pembayaran,
      sisa_bayar : row.nominal_kembali
    }
    // console.log(row);
    if(row.nama_bank === undefined && row.jenis !== "CASH"){
      ToastNotification("info", "Nama Bank tidak boleh kosong");
      return false;
    }
    if(row.cash === undefined || row.cash === "0"){
      ToastNotification("info", "Nominal tidak boleh kosong");
      return false;
    }
    setItem('sisa_bayar_pesanan',datacek)
    let data = JSON.parse(localStorage.getItem("trx_pemabayran_ambil_pesanan")) || [];
    if (row.jenis !== "CASH" && !data.length) {
      if (row.kembali.replace(/[^0-9]/g, "") - row.cash < 0) {
        ToastNotification("info", "Pembayaran tidak boleh lebih dari sisa");
        return false;
      }
    } else {
      if(row.jenis !== "CASH"){
      if (row.kembali.replace(/[^0-9]/g, "") - row.cash < 0 && parseInt(row.kembali) !== 0){
        ToastNotification("info", "Pembayaran tidak boleh lebih dari sisa");
        return false;
      } else {
        console.log(row.grand_total_pembayaran);
        if (data.reduce((a, b) => a + b.nominal, 0) >= row.grand_total_pembayaran) {
          ToastNotification("info", "Pembayaran Sudah Cukup1");
          return false;
        }
        }
      }
    }
   

    let cash = data.findIndex((hasil) => hasil.jenis === "CASH");
    if (cash >= 0) {
      if (row.kembali.replace(/[^0-9]/g, "") - row.cash < 0 && data.reduce((a, b) => a + b.nominal, 0) > row.grand_total_pembayaran) {
        ToastNotification("info", "Pembayaran Sudah Cukup2");
        return false;
      } else {
        if (data.reduce((a, b) => a + b.nominal, 0) >= row.grand_total_pembayaran) {
          ToastNotification("info", "Pembayaran Sudah Cukup3");
          return false;
        }
      }
    }

    let index = data.findIndex((hasil) => hasil.jenis === row.jenis);
    if (index === -1) {
      let keterangan = "";
      if (row.jenis === "CASH") {
        keterangan = "CASH";
      } else if (row.jenis === "TRANSFER") {
        keterangan = row.no_card + " - " + row.nama_bank;
      } else {
        keterangan = row.nama_bank;
      }
      let hasil = {
        jenis: row.jenis,
        nominal: parseInt(row.cash) * parseInt(row.fee || 0) / 100 + parseInt(row.cash),
        nama_bank: row.nama_bank,
        fee : row.jenis === "CREDIT" ? row.fee || 0 : 0 ,
        no_card: row.no_card || "-",
        keterangan: keterangan,
        kembali: row.nominal_kembali
      };
      data.push(hasil);
      this.setState({
        masuk: true,
        kembalian: row.kembali,
      });
      setItem("trx_pemabayran_ambil_pesanan", data);
    } else {
      let data_pembayaran = getItem("trx_pemabayran_ambil_pesanan");
      data_pembayaran = data_pembayaran.map((element2) => {
        if (element2.jenis === row.jenis) {
          element2.nominal += parseInt(row.cash);
        }
        return element2;
      });
      setItem("trx_pemabayran_ambil_pesanan", data_pembayaran);
      this.setState({
        status: true,
        kembalian: row.kembalian,
      });
      // ToastNotification(
      //   "info",
      //   "Pembayaran dengan " + row.jenis + " sudah ada"
      // );
    }
  }

  render() {
    return (
      <>
        <HeadInputPembayaran onSubmit={(data) => this.simpanPembayaran(data)} />
        <hr />
        <form
          onSubmit={this.props.handleSubmit}
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
          autoComplete="off"
        >
          <Tabel
            // handleClick={() => this.simpanpembbayaran()}
            keyField="jenis"
            // tambahData={true}
            // btnText="Tambah Pembyaran"
            exportCsv={false}
            data={getItem("trx_pemabayran_ambil_pesanan") || []}
            columns={this.state.columns}
            empty={true}
            pagination="off"
            textEmpty="Data Pembayaran Kosong"
          />
          <div className="row">
            <div className="col-12 text-right">
              <button
                className="btn btn-primary"
                disabled={this.props.isLoadingPembayran}
                onClick={() => this.props.simpanPembayaran()}
                type="button"
              >
                {this.props.isLoadingPembayran ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Diproses
                  </>
                ) : (
                  "Bayar Sekarang"
                )}
              </button>
            </div>
           
          </div>
        </form>
      </>
    );
  }
}

ModalPembayaran = reduxForm({
  form: "ModalPembayaran",
  enableReinitialize: true,
  validate: ValidasiPembayaran,
})(ModalPembayaran);

export default connect(maptostate)(ModalPembayaran);
