/* eslint-disable no-undef */
import { React, connect, reduxForm, InputDate, Field, getTglSystem } from "../../../components/helpers/library";
// import Loading from 'react-fullscreen-loading';

class HeadPesanan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  handleSubmit(data) {}
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  render() {
    return (
      <div>
        <form
          onSubmit={this.props.handleSubmit}
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
          autoComplete="off"
        >
          <div className="row">
            <div className="col-lg-4">
              <Field
                name="tgl_awal"
                component={InputDate}
                label="Tanggal Dari"
                type="text"
                selected={this.state.tgl_awal}
                onChange={(date) => this.setStartDate(date)}
                placeholder="Masukan Tanggal Dari"
                onFocus={() => setTimeout(() => {
                  HideKeyboard.postMessage("")
                }, 100)}
              />
            </div>

            <div className="col-lg-4">
              <Field
                name="tgl_akhir"
                component={InputDate}
                type="text"
                selected={this.state.tgl_akhir}
                onChange={(date) => this.setLastDate(date)}
                label="Tanggal Akhir"
                placeholder="Masukan Tanggal Akhir"
                onFocus={() => setTimeout(() => {
                  HideKeyboard.postMessage("")
                }, 100)}
              />
            </div>

            <div className="col-lg-4">
              <div className="text-right">
                <label>&nbsp;</label>
                <button
                  type="submit"
                  value="PDF"
                  name="type_btn"
                  className="btn btn-primary btn-block"
                >
                  {this.props.isLoading ? (
                    <>
                      <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                      Mencari
                    </>
                  ) : (
                    "Cari Pesanan"
                  )}
                </button>
              </div>
            </div>
          </div>
           {/* {/* {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
        </form>
      </div>
    );
  }
}

HeadPesanan = reduxForm({
  form: "HeadPesanan",
  enableReinitialize: true,
})(HeadPesanan);

export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadPesanan);
